import React, { useState, useEffect } from 'react';
import { BrowserRouter, Switch, Route, Redirect } from 'react-router-dom';
import { MuiThemeProvider } from '@material-ui/core/styles';
import Header from './components/Header';
import Home from './components/Home';
import Background from './components/Background';
import Skills from './components/Skills';
import Projects from './components/Projects';
import Footer from './components/Footer';
import { HashLoader } from 'react-spinners';


import muiTheme from './theme';
import './App.css';

const imgPath = `${process.env.PUBLIC_URL}/assets/img/`;

function App() {
  const [load, setLoad] = useState(false);
  const [mainData, setMainData] = useState({});

  useEffect(() => {
    const fetchMainData = async () => {
      const res = await fetch("./assets/data/main.json");
      const data = await res.json();
      setMainData(data);
    };
    setLoad(true);
    fetchMainData();
    setLoad(false);
    console.log("WELCOME TO MY HOMEPAGE");
  }, []);
  
  const setLoading = (bool) => {
    setLoad(bool);
  };
  const setSnackStack = (msg) => {
    // later
  };

  if (load) {
    return (
      <div className='sweet-loading'>
        <HashLoader
          sizeUnit={"px"}
          size={150}
          color={'#0d3e69'}
          loading={load}
        />
      </div>
    );
  }

  return (
    <MuiThemeProvider theme={muiTheme}>
      <div className="App">
        <BrowserRouter basename={process.env.PUBLIC_URL}>
          <header className="App-header" id="Header"><Header imgPath={imgPath} avatar={mainData.avatar} name={mainData.name} lastupdate={mainData.lastupdate} /></header>
          <main className="App-main" id="Main">
            <Switch>
              <Redirect exact from="/home" to="/" />
              <Route exact path="/" render={() => <Home imgPath={imgPath} loader={setLoading} notif={setSnackStack} />} />
              <Route exact path="/background" render={() => <Background imgPath={imgPath} loader={setLoading} notif={setSnackStack} />} />
              <Route exact path="/skills" render={() => <Skills imgPath={imgPath} loader={setLoading} notif={setSnackStack} />} />
              <Route exact path="/projects" render={() => <Projects imgPath={imgPath} loader={setLoading} notif={setSnackStack} />} />
            </Switch>
          </main>
          <footer className="App-footer" id="Footer"><Footer /></footer>
        </BrowserRouter>

      </div>
    </MuiThemeProvider>
  );
}

export default App;
