import React, { useState, useEffect } from 'react';
import { CircularProgressbarWithChildren, buildStyles } from 'react-circular-progressbar';
import {
  Grid,
} from '@material-ui/core';

import 'react-circular-progressbar/dist/styles.css';
import "./Skills.scss";
import SkillsPlaceholder from './SkillsPlaceholder';

const card = s => (
  <Grid className="content-item" key={s.name} item xs={6} sm={4} md={3} lg={2}>
    <CircularProgressbarWithChildren
      value={s.amount}
      strokeWidth={3}
      counterClockwise
      background
      backgroundPadding={2}
      styles={buildStyles({
        backgroundColor: "rgba(176,66,180,0.75)",
        pathColor: "#ededed",
        trailColor: "transparent"
      })}
    >
      <div className="skill-text">{s.name}</div>
    </CircularProgressbarWithChildren>
  </Grid>
);

const type = (t, ss) => (
  <div key={t}>
    <h2 className="info-flex info-title">
      <i className="fa fa-spinner info-icon" />
      <div>{String(t[0]).toUpperCase() + Array.from(t.replace(/(\w+)(\d+)/, "$1 $2")).filter((c, k) => k !== 0 ? c : "").join("")}</div>
    </h2>
    <Grid container justify="center" className="info-content" spacing={2}>
      {
        ss.map(s => card(s))
      }
    </Grid>
  </div>
);

export default function Skills(props) {
  const { loader } = props;
  const [data, setData] = useState(null);
  const scrollY = window.scrollY;
  useEffect(() => {
    if(scrollY){
      setTimeout(() => {
        window.scrollTo(0, 0);
      }, 100);
    }
  }, [scrollY]);
  useEffect(() => {
    const fetchData = async () => {
      const res = await fetch("./assets/data/skills.json");
      const data = await res.json();
      setData(data);
    };
    loader(true);
    fetchData();
    loader(false);
  }, [loader]);

  if (!data) {
    return (
      <SkillsPlaceholder />
    );
  }

  return (
    <div id="Skills">
      <div className="info">
        {
          Object.keys(data).map(e => type(e, data[e]))
        }
      </div>
    </div>
  );
}
