import React, { useState, useEffect } from 'react';

import "./Projects.scss";
import ProjectsPlaceholder from './ProjectsPlaceholder';

const card = w => (
  <div key={w.name}>
    <div className="content-top info-flex">
      <i className={w.icon} />
      <div>{w.name}</div>
      {
        w.star &&
        <i className="fas fa-star star fa-sm" />
      }
    </div>
    <div className="content-middle info-flex">
      <i className="fas fa-paperclip" />
      <div>{w.for}</div>
    </div>
    <div className="content-bottom">
      <div>{w.desc}</div>
      <a href={w.url} target="_blank" rel="noopener noreferrer"><i className="fa fa-external-link-alt" /></a>
    </div>
  </div>
);

const year = (y, d) => (
  <div key={y}>
    <h2 className="info-flex info-title">
      <i style={{color: d.color}} className="fa fa-calendar-alt" />
      <div>{String(y[0]).toUpperCase() + Array.from(y.replace(/(\w+)(\d+)/, "$1 $2")).filter((c, k) => k!==0?c:"").join("")}</div>
    </h2>
    <div className="info-flex info-date">
      <i className="far fa-clock" />
      <div>{d.date}</div>
    </div>
    <div className="info-content">
      {
        d.work && d.work.map(w => card(w))
      }
    </div>
  </div>
);

export default function Teaching(props) {
  const { loader } = props;
  const [data, setData] = useState(null);
  const scrollY = window.scrollY;
  useEffect(() => {    
    if(scrollY){
      setTimeout(() => {
        window.scrollTo(0, 0);
      }, 100);
    }
  }, [scrollY]);
  useEffect(() => {
    const fetchData = async () => {
      const res = await fetch("./assets/data/projects.json");
      const data = await res.json();
      setData(data);
    };
    loader(true);
    fetchData();
    loader(false);
  }, [loader]);
  if (!data) {
    return (
      <ProjectsPlaceholder />
    );
  }
  return (
    <div id="Projects">
      <div className="info">
        {
          Object.keys(data).reverse().map(e => year(e, data[e]))
        }
      </div>
    </div>
  );
}
